.cardProductSection {
  display: flex;
  width: 272px;
  height: 506px;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  flex-shrink: 0;
  border-radius: 8px;
  border: 1px solid var(--Gray-Elements, #E2E6E9);
  background: var(--Gray-White, #FFF);
}

.productImages {
  width: 208px;
  height: 196px;
  // background: lightgray 50% / contain no-repeat;
}

.productTitles {
  color: var(--Gray-Primary, #0F0F11);
  font-size: 14px;
  font-weight: 600;
  line-height: 21px;
}

.productPrices {
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 8px;
}

.priceNow {
  color: var(--Gray-Primary, #0F0F11);
  font-size: 22px;
  font-weight: 800;
  line-height: 140%;
}

.priceOld {
  color: var(--Gray-Secondary, #89939A);
  font-size: 22px;
  font-weight: 500;
  line-height: normal;
  text-decoration-line: strikethrough;
}

.techSpecs {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.techSpectName {
  display: flex;
  flex-direction: column;
  color: var(--Gray-Secondary, #89939A);
  font-size: 12px;
  font-weight: 600;
  line-height: normal;
}

.techSpecsSpecs {
  display: flex;
  flex-direction: column;
  color: var(--Gray-Primary, #0F0F11);
  text-align: right;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.cardButtons {
  display: flex;
}

.addCard {
  display: flex;
  height: 40px;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background: var(--Accent, #216CFF);
  outline: transparent;
  gap: 8px;
}

.like {
  width: 40px;
  height: 40px;
  border-radius: 48px;
  border: 1px solid var(--gray-icons-placeholders, #B4BDC3);
}

.heardIcon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}


